//Main page
import React, { Component } from 'react';
import Menu2 from '../components/Menu2';
import Header from '../components/Header';
import Books from '../components/Books';

class Index extends Component {
	constructor(props) {
			super(props);
			this.state = {currentTab: "Books"};

			this.tabClick = this.tabClick.bind(this);
	}

	static async getInitialProps() {
		console.log('fetching data...');
	    const res = await fetch('https://www.googleapis.com/books/v1/volumes?q=harrypotter');
		//const jsonData = await res.json();
		//console.log("Type of server response: " + (typeof jsonData));
		/*
		var books = [];
		if (res != undefined){
			for (var i = 0; i < res.items.length; i++) {
	    	    var item = res.items[i];
	        	item.volumeInfo.title;
      		}

		} else {
			console.log("User is undefined");
		}
		//for (var i = 0; i < res.items.length; i++) {
	    //    var item = res.items[i];
	        //item.volumeInfo.title;
      	//}*/

		//console.log('Data is fetched: ' + jsonData.items);

		return {
		    bookData: res.items
  		}
	}

	tabClick(ind) {
    //console.log('Wrapper: Click happened: ' + ind);
    	this.setState({currentTab: ind});
  	}

	render() {
		return (
		  <div className = 'rootDiv'>
		  		<Header></Header>
			    <Menu2 chosenTab={this.state.currentTab} clickEvent={this.tabClick}></Menu2>
			    <Books bookData={this.props.bookData}></Books>
			    
			    <style global jsx>{`
			    	* {
			    		box-sizing: border-box;
			    		margin: 0;
		  			 	padding: 0;
			    	}
			      html, body {
			      	 height: 100%;
			      	 background: #202020;
			      	 width: 100%;
		  			 font-family: sans-serif;
		  			 min-width: 200px;
			      }
			      body {
			      	 background-image: 
						url('/static/book1.jpg');
						background-position: center;
						background-repeat: no-repeat;
						background-size: cover;
			      }
			      .rootDiv {
			      		height: 100%;
			      		width: 100%;
			      }
			      .title {
			    		height: 200px;
			    		display: flex;
			    		flex-flow: column wrap;
			    		align-items: center;
			    	}
			    	h1 {
			    		text-align: center;
			    		align-self: center;
			    		display: inline;
			    		margin: auto;
			    		background-color: black;
			    		text-transform: uppercase;
			    		color: white;
			    		padding: 5px 10px 5px 10px;
			    		opacity: 0.5;
			    	}
			    	nav {
			    		height: 45px;
			    		background-color: #444444;
			    		display: flex;
			    		flex-flow: row nowrap;
			    		align-items: center;
			    		justify-content: center;
			    	}
			    	a {
			    		padding-right: 10px;
			    		padding-left: 10px;
			    		padding-top: 13px;
			    		padding-bottom: 13px;
			    		text-decoration: none;
						outline: none;
						color: white;
			    	}
			    	a:hover {
			    		background-color: grey;
			    	}
			    	.active {
			    		background-color: grey;
			    	}
			    	.bookList {
			    		color: white;
			    	}
		    	`}</style>
		  </div>
  		);//end of return
	} //end of render
} //end of component

export default Index;







